#root{
  width: 100%;
}

html{
  width: 100vw;
}
body {
  min-width: 100vw;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main{
    flex:1 0;
    background-color: grey;
    display: flex;
}
.contentdisp{
    display: flex;
    flex-direction: column;
    flex: 1 0;
}
.header{
    flex: 1 0;
    background-color: #4E4E4E;
    display:flex;
    align-items: center;
    flex-direction: row;
    
    
}
.media-art{
    flex: 0 0 50px;
    padding-left: 40px;
    
}
.nowplaying-container{
    display: flex;
    flex-direction: column;
    /* align-self: baseline; */
    color:whitesmoke;
    margin-left: 50px;
    font-family: 'Cinzel', serif;
    font-weight: normal;
    height: 80%;
}
 
.nowplaying{
    margin-bottom: auto;
}


p{
    margin: 0px;
    font-size:1em;
}

.sm-nav{
    display: flex;
    align-items: center;
}

.sm-nav > div{
 
    font-size: 12px;
    display: inline;
    flex:1 0;
    max-width: 70px;;
    
}

.bg-color-change > p{
    padding: 10px;
}


.bg-color-change :hover{
    background-color:rgb(175, 175, 175);
    cursor: pointer;
}

.nowplaying > h1{
    margin-top: 0%;
}

.bg-color-change> p{
    margin-bottom: 0px;
}

@media only screen and (max-width: 600px) {
    .nowplaying > h1{
        font-size: 18px;
        line-height: 30px;
    }
  }
.playlist{
    flex: 1 1;
    background-color: #FFFFFF;
    padding-top: 20px;
}
.playlist-container{
    display: flex;
    flex-direction: row;
    padding: 10px 10px;
    font-size: 12px;
    font-weight: normal;
    width: 95%;
    margin: 0 auto;
}



.setting-equal-width{
    flex: 1 0;
}

.ps-type{
    flex: 0 0 auto ;
    padding: 1px;
    font-size: 8px;
    border: 1px solid black
}
.grey{
    background-color:#F2F2F2;
    border : 1px solid rgb(201, 201, 201); 
}

.white{
    background-color: white;
}
.sidebar{
    flex: 0 0 40px;
    background-color: #F2F2F2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}
 
.sidebar > i{
    flex:0 0 60px;
    padding-top:10px;
    padding-bottom: 10px; 

}

.sidebar > i:first-child{
    margin-top: 100%;
}

.sidebar > i:hover{
    cursor: pointer;
    color: rgb(173, 173, 173)
}
.footer{
    background-color: #5073A7;
    flex: 0 0 100px;
    display: flex;
    align-items: center;
}
.media-art-container{
    margin-right: auto;
    padding: 0px;
    padding-left: 10px;

}

.left-container{
    display: flex;
    align-items: center;

}

.left-container > .img-container{
    height: 80px;
    width: 60px;
    
}

.img{
    width: 60px;
    height: 80px;
}

.track-info-container{
    margin-left: 20px;
    color:white;
    font-weight: normal;
    font-size: 15px;
    
}

.track-info-container :hover{
    cursor: pointer;
    color:rgb(185, 185, 185);
}

.first-p {
    padding-bottom: 10px;
}


.footer-nav{
    padding-right: 10px;
    display: flex;
    align-items: center;
}

.footer-nav > i {
    padding: 45px;
    margin-left: 5px;
    color: white;
    font-size: 12px;
}

.footer-nav > i:hover{
    background-color: rgb(48, 68, 99);
    cursor: pointer;
}
.app-container{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 100%;
}
